<template>
  <v-container fill-height>
    <v-row justify-lg="center" align="center">
      <v-col cols="12">
        <v-form
           ref="form"
    v-model="valid"
    lazy-validation
        >
        <v-card class="mx-auto" max-width="374">
          <v-card-text class="pb-0">
            <v-text-field
            v-mask="'#### #### #### ####'"
            :masked="false"
              dense
              outlined
              :rules="cardNumberRules"
              label="Número de tarjeta"
              v-model="cardNumber"
          
              required
            ></v-text-field>

            <v-text-field
            :rules="nameRules"
              dense
              outlined
              name="Nombre"
              label="Nombre"
              v-model="name"
              
            ></v-text-field>
            <v-row>
              <v-col>
                <v-text-field
                :rules="expirationDateRules"
                  dense
                  outlined
                  name="Fecha de expiración"
                  v-model="expirationDate"
                  label="Fecha expiración"
                  v-mask="'##/##'"
                  placeholder="MM/YY"
                  
                ></v-text-field>
              </v-col>
              <v-col>
                <v-text-field
                :rules="cvcRules"
                  dense
                  outlined
                  name="CVC"
                  label="CVC"
                  v-model="cvc"
                  type="password"
                 
                ></v-text-field>
              </v-col>
            </v-row>
          </v-card-text>
          <v-card-actions class="pt-0">
            <v-row>
              <v-col cols="12">
                <v-btn
                :loading="loading"
                :disabled="loading"
                 @click="validate"  block color="primary">Pagar</v-btn>
              </v-col>
            </v-row>
          </v-card-actions>
        </v-card>
        </v-form>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import luhn from "luhn";
import {validateDate} from '../helpers/validateDate'
import { Kushki } from "@kushki/js";
import axios from 'axios'
const chargeAmount = 49.99;
const chargeCurrency = "USD";
const KUSHKI_PUBLIC_MERCHANT_ID = "bc14bda1ebec414295558f0fea6a07de";
const header = {
  "Private-Merchant-Id": "44e8f18b2c944e28be61e23e2584537e",
  'content-type': 'application/json'
}
const kushki = new Kushki({
  merchantId: KUSHKI_PUBLIC_MERCHANT_ID,
  inTestEnvironment: true,
  regional: false
});


export default {
  
  
  name: "HelloWorld",

  data: () => ({
    loading:false,
    valid: true,
    cardNumber:'',
    name:"",
    expirationDate:"",
    cvc:"",
    nameRules:[
     v => !!v || 'El nombre es requerido',
       
  ],
    cardNumberRules:[
     v => !!v || 'Número de tarjeta requerido',
       (v) => /^\d{16,22}$/.test(v.replace(/ /g,"")) || "Número de tarjeta incompleto",
  (v)=> luhn.validate(v)|| "Número de tarjeta incorrecto"
  ],
    expirationDateRules:[
      v => !!v || 'Expiración requerida',
      (v)=>validateDate(v)||"Expiración vencida",
      (v) => /^(0[1-9]|1[0-2])\/\d\d/.test(v) || "Expiración incorrecta",
      
    ],
    cvcRules:[
     v => !!v || 'CVC está incompleto',
     (v) => /^\d{3,4}$/.test(v) || "CVC está incompleto",
  ],

  }),
  created(){
    

 

 /*  const kushki = new Kushki({
  merchantId: 20000000107842740000, // Your public merchant id 
  inTestEnvironment: true,
  
}); */

  },
  watch:{
    expirationDate (value){
      console.log(validateDate(value))
    }
  },
     methods: {
      validate () {
        if(this.$refs.form.validate()){
           this.loading=true
            kushki.requestToken({
      amount: chargeAmount,
      currency: chargeCurrency,
      card: {
        name: this.name,
        number: this.cardNumber.replace(/ /g,""),
        cvc: this.cvc,
        expiryMonth: this.expirationDate.split("/")[0],
        expiryYear: this.expirationDate.split("/")[1]

      },
    }, (response)=>{
       if (!response.code) {
        console.log(response);
        
        axios.post("https://api-uat.kushkipagos.com/card/v1/charges", {
          token: response.token,
          amount: {
            subtotalIva: 0,
            subtotalIva0: chargeAmount,
            ice: 0,
            iva: 0,
            currency: "USD"
          },
          metadata: {
            contractID: "157AB"
          },
          fullResponse: true
        },
          { headers: header }
        ).then((response) => {
          
          this.$notify({
  group: 'foo',
  title: 'Aviso!',
  type:"success",
  text: 'Transación exitosa'
})
          
        
        })
          .catch((error) => {
           
            if (error.response.data) {
              console.log(error.response.data.message);
                    this.$notify({
  group: 'foo',
  title: 'Aviso!',
  type:"error",
  text: error.response.data.message
})
            } else {
              console.error(error);

                 this.$notify({
  group: 'foo',
  title: 'Aviso!',
  type:"error",
  text: error
})
              
            }
          })
          .finally(() => {
            console.log("ahora estoy aqui")
             this.loading=false
             this.reset ();
             this.resetValidation ();
          });



      } else {
        
                  this.$notify({
  group: 'foo',
  title: 'Aviso!',
  type:"error",
  text: response.message
})

         this.loading=false
         this.reset ();
             this.resetValidation ();
        console.error('Error: ', response.error, 'Code: ', response.code, 'Message: ', response.message);
      }


    } );
























       
         
      
        }else{
          alert("Por favor, ingrese la información del formulario")
        }
      },
      reset () {
        this.$refs.form.reset()
      },
      resetValidation () {
        this.$refs.form.resetValidation()
      },
    },

};
</script>
<style>
  .custom-loader {
    animation: loader 1s infinite;
    display: flex;
  }
  @-moz-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @-webkit-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @-o-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
</style>